              :title="topic.user.nickname"
            >
              <img :src="topic.user.smallAvatar" class="avatar" />
            </a> -->
          </div>
          <div class="topic-header-center">
            <h1 class="topic-title" itemprop="headline">
              <a
                :target="blank"
                :href="'/tiaoji' + topic.post_url"
                :title="topic.post_title"
                >{{ topic.post_title }}</a
              >
              <!-- <span class="text-top">{{ getTopText(topic) }}</span> -->
            </h1>
            <div class="topic-meta">
              <!-- <span
                class="meta-item"
                itemprop="author"
                itemscope
                itemtype="http://schema.org/Person"
              >
                <a :href="'/user/' + topic.user.id" itemprop="name">{{
                  topic.user.nickname
                }}</a>
              </span> -->
              <span class="meta-item">
                <TiaojiSectionTime
                  :create-time="topic.post_datetime"
                  :hide="false"
                ></TiaojiSectionTime>
                <!-- <time
                  :datetime="
                    topic.createTime | formatDate('yyyy-MM-ddTHH:mm:ss')
                  "
                  itemprop="datePublished"
                  >{{ topic.createTime | prettyDate }}</time
                > -->
              </span>
              <span class="meta-item">
                <span
                  v-for="tag in topic.tags"
                  :key="tag.tagName + tag.tagId"
                  class="tag"
                >
                  <!-- <a :href="'/topics/tag/' + tag.tagId">{{ tag.tagName }}</a> -->
                  <a>{{ tag.tagName }}</a>
                </span>
                <span :target="blank" class="">
                  <a class="tag" @click="clickCollapse(index)"
                    >展开&nbsp;
                    <font-awesome-icon
                      v-if="!isCollapsedArray[index]"
                      icon="angle-down"
                    />
                    <font-awesome-icon
                      v-if="isCollapsedArray[index]"
                      icon="angle-up"
                    />
                  </a>
                </span>
              </span>
            </div>
          </div>
          <!-- <div class="topic-header-right">
            <div class="like">
              <span
                :class="{ liked: topic.liked }"
