            >
          </div> -->
        </div>
        <div
          class="card-content"
          :class="{ 'is-hidden': isCollapsedArray[index] }"
        >
          <!-- <slot v-if="isCollapsedArray[index]"> -->
          <div
            style=" font-size: 13px;
                color: black;"
            v-html="formatText2Html(buildCollapseBody(topic))"
          ></div>
          <!-- </slot> -->
        </div>
        <!-- <Panel
          id="frontpage-firstpanel"
          title="rr"
          :on-collapsed="onCollapseBack"
          >Some Content
          <template slot="title">
            <div class="footer-buttons">
              <button class="button is-primary is-pulled-left">Button</button>
              <button class="button is-danger is-pulled-right">Oh no!</button>
            </div>
          </template>
        </Panel> -->
      </article>
    </li>
  </ul>
</template>

<script>
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import utils from '~/common/utils'
// import Panel from '~/components/Panel'
import TiaojiSectionTime from '~/components/tiaoji/TiaojiSectionTime'

library.add(faAngleUp, faAngleDown)
export default {
  components: {
    // Panel,
    FontAwesomeIcon,
    TiaojiSectionTime
  },
  props: {
    topics: {
      type: Array,
      default() {
        return []
      },
      required: false
    },
    showAd: {
      type: Boolean,
      default: false
    },
    openNewPage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      blank: '',
      isCollapsed: true,
      isCollapsedArray: []
    }
  },
  watch: {
    topics(newValue, oldValue) {
      if (this.topics && this.topics.length !== undefined) {
        this.isCollapsedArray = new Array(this.topics.length).fill(true)
      }
    }
  },
  mounted() {
    if (this.openNewPage) {
      this.blank = '_blank'
    }
    // console.log('topics:' + JSON.stringify(this.topics))
    if (this.topics && this.topics.length > 0) {
      this.isCollapsedArray = new Array(this.topics.length).fill(true)
    }

    // console.log(JSON.stringify(this.isCollapsedArray))
  },
  methods: {
    async clickCollapse(index) {
      if (this.topics[index].description === '') {
        try {
          const topic = await this.$axios.get(
            '/api/tiaoji' + this.topics[index].post_url
          )
          // console.log('topic:' + JSON.stringify(topic))
          this.topics[index].description = topic.description
        } catch (e) {
          console.log(e)
        }
      }
      this.$set(this.isCollapsedArray, index, !this.isCollapsedArray[index]) // 改变数组

      // console.log('detail:' + JSON.stringify(topic))
    },
    buildCollapseBody(topic) {
      return (
        '学校：' +
        topic.colleage_name +
        '\n' +
        '专业：' +
        this.formatMajorsConntent(topic) +
        '\n\n' +
        topic.description
      )
    },
    formatMajorsConntent(topic) {
      let result = '-'
      if (topic.major_top_class !== '') {
        result = topic.major_top_class
      }

      if (topic.major_top_class !== '') {
        result += '->' + topic.major_second_class
      }

      if (topic.major_top_class !== '') {
        result += '->' + topic.major_third_class
      }

      return result
    },
    formatText2Html(description) {
      let html = description
      if (html) {
        html = html.replace(/ /g, '&nbsp;')
        html = html.replace(/\n/g, '<br/>')
      }
      return html
    },
    getTopText(topic) {
      const topType = topic.topType ? topic.topType : 0
      return utils.getTopText(topType)
    },
    onCollapseBack(state) {
      // console.log(state)
    },
    async like(topic) {
      this.isCollapsed = !this.isCollapsed
      try {
        await this.$axios.post('/api/topic/like/' + topic.topicId)
        topic.liked = true
        topic.likeCount++
      } catch (e) {
        if (e.errorCode === 1) {
          this.$toast.info('请登录后点赞！！！', {
            action: {
              text: '去登录',
              onClick: (e, toastObject) => {
                utils.toSignin()
              }
            }
          })
        } else {
          this.$toast.error(e.message || e)
        }
      }
    }
  }
}
</script>

